.dataTables_wrapper .dataTables_info {
  clear: none;
  margin-left: 10%;
}

.dataTables_wrapper .dataTables_length {
  padding-top: 0.755em;
}

.dataTables_wrapper .dataTables_filter input {
  border: 1px solid lightgray;
  line-height: 1.5em;
}

button.dt-button-warning,
a.dt-button-warning,
div.dt-button-warning {
  background: #f2dede linear-gradient(to bottom, #f2dede 0%, #e7c3c3 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, StartColorStr='#f2dede', EndColorStr='#e7c3c3');
}

button.dt-button-warning:active:not(.disabled):hover:not(.disabled),
button.dt-button-warning.active:not(.disabled):hover:not(.disabled),
a.dt-button-warning:active:not(.disabled):hover:not(.disabled),
a.dt-button-warning.active:not(.disabled):hover:not(.disabled),
div.dt-button-warning:active:not(.disabled):hover:not(.disabled),
div.dt-button-warning.active:not(.disabled):hover:not(.disabled) {
  box-shadow: inset 1px 1px 3px #999999;
  background: #e0cece linear-gradient(to bottom, #e0cece 0%, #d1b2b2 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, StartColorStr='#e0cece', EndColorStr='#d1b2b2');
}

button.dt-button-warning:hover:not(.disabled),
a.dt-button-warning:hover:not(.disabled),
div.dt-button-warning:hover:not(.disabled) {
  background: #e0cece linear-gradient(to bottom, #e0cece 0%, #d1b2b2 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, StartColorStr='#e0cece', EndColorStr='#d1b2b2');
}

.dataTable {
  width: 100% !important;
}

.dataTable thead tr:hover {
  background-color: white !important;
}

.dataTable tbody .innerDetails {
  margin-left: 30px;
}

.dataTable tbody tr.group,
.dataTable tbody tr.group:hover {
  background-color: white !important;
  cursor: pointer;
}

.dataTable tbody tr.group td {
  font-weight: bold;
}

.dataTable tbody tr td.editable {
  cursor: url(../images/edit.png), cell;
}

.dataTable tbody tr td.toggle-childrow::before {
  font-size: 1.1em;
  font-family: FontAwesome;
  content: "\F196";
}

.dataTable tbody tr:hover {
  cursor: default;
}

.dataTable tbody tr:hover td.toggle-childrow::before {
  content: "\F0FE";
}

.dataTable tbody tr.expanded td.toggle-childrow::before {
  content: "\F147";
}

.dataTable tbody tr.expanded:hover td.toggle-childrow::before {
  content: "\F146";
}

.dataTable tbody tr.loading td.toggle-childrow::before {
  content: none;
}

.dataTable tbody tr.loading td.toggle-childrow {
  background: url(../images/loading-fb.gif) no-repeat center center;
}

.dataTable tbody tr.childrow:hover {
  background-color: white !important;
}

